/* =======================================
   === SKALA & AKSESIBILITAS GLOBAL ===
======================================= */
html[class*="font-scale-"],
.content-wrapper[class*="font-scale-"] {
  font-size: 100% !important;
}

.content-wrapper.font-scale-50  { font-size: 50% !important; }
